.dashboard-container {
  padding: 40px;
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.dashboard-loading {
  margin-top: 100px;
  text-align: center;
  font-size: 20px;
  color: gray;
}

.dashboard-actions {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  gap: 15px;
  flex-wrap: wrap;
}

.dashboard-actions button {
  background-color: #007bff;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: 0.2s ease;
}

.dashboard-actions button:hover:not(:disabled) {
  background-color: #0056b3;
}

.dashboard-actions button:disabled {
  background-color: #999;
  cursor: not-allowed;
}
